body {
  background-color: #fcfcfc;
}

.search-container {
  min-height: 50px;
  background-color: #0352fc;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form input {
  min-height: 30px;
  min-width: 250px;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
}

.search-form button {
  min-height: 30px;
  min-width: fit-content;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin: 30px;
  display: flex;
}

.photo-card {
  border-radius: 5px;
  flex-basis: calc(25% - 22.5px);
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.photo-card > img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.img-container > img {
  box-sizing: content-box;
  width: 400px;
  height: 70%;
  object-fit: contain;
  object-position: center;
}

.img-container {
  width: 100%;
  height: 100%;
}

.info {
  border-width: 0 1px 1px;
  justify-content: space-around;
  padding-top: 15px;
  padding-bottom: 15px;
